import Vue from 'vue'
const checkUrlParam = {
  methods: {
    checkUrlParam () {
      const myWeb3 = Vue.prototype.$myWeb3 // 以metamask的提供器，获取web3对象
      // 浏览器会忽略#标志后面的数据，所以vue如果使用hash路由模式，则location.search获取不到值,需要启用history模式
      let urlParams = window.location.search // 获取url参数，审核是否具有邀请链接; todo 注意，在masternode中的 refer link不是 ?to=作为前缀s
      let referralAddress = null // 交易数据
      if (urlParams && urlParams.substr(0, 4) === '?to=') {
        referralAddress = urlParams.substr(4) // buy接口的referralAddress
        if (referralAddress && myWeb3 && myWeb3.utils.isAddress(referralAddress)) {
          return referralAddress
        }
      }
      return false
    }
  }
}

export {checkUrlParam}



