// 进入['Tasks', 'Withdraw', 'Statistics', 'MasterNode']页面时，过滤直推地址的合法性
import Vue from 'vue'
import store from '../store/index'
const urlFilter = {
  // 全局导航守卫不可能过来 fromParam为真toParam为假的数据
  beforeRouteEnter (to, from, next) {
    const fromQuery = from.query
    const fromParam = fromQuery.to
    const toParam = to.query.to
    const myWeb3 = Vue.prototype.$myWeb3 // 以metamask的提供器，获取web3对象

    if (myWeb3) {
      if (toParam && myWeb3.utils.isAddress(toParam)) {
        if (store.state.metamask.urlStatus !== 1) { // urlStatus !== 1 时，提交改变状态
          store.commit('metamask/setUrlStatus', 1)
        }
        next()
      } else if (fromParam && myWeb3.utils.isAddress(fromParam)) {
        next({
          path: to.path,
          query: fromQuery
        })
      } else {
        // todo 提交一个状态，以便提示用户的直推地址不合法
        if (store.state.metamask.urlStatus !== 0) { // urlStatus === 1 时，提交改变状态
          store.commit('metamask/setUrlStatus', 0)
        }
        next({
          path: '/purchase'
        })
      }
    } else {
      if (toParam) {
        next({
          path: '/purchase',
          query: fromQuery
        })
      } else {
        next({
          path: '/purchase'
        })
      }
    }
  }
}

export {urlFilter}



